.add {
  position: fixed;
  bottom: 5px;
  right: 0;
  display: flex;
  align-items: center;
  background: linear-gradient(rgba(0, 0, 0, 0.398), rgba(14, 243, 174, 0.398), rgba(0, 0, 0, 0.398));
  padding-block: 20px;
  border-radius: 50px;
  padding-inline: 30px;
  visibility: hidden;
  opacity: 0;
  transition: 0.3 ease-in-out;
  z-index: 999;
  cursor: pointer;
}
@media (max-width: 600px) {
  .add {
    padding-block: 10px;
    padding-inline: 20px;
  }
}
.add.active {
  visibility: visible;
  opacity: 1;
}
.add:hover {
  transform: scale(0.95);
}
.add ::before {
  position: absolute;
  content: "";
  inset: 3px;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.962);
  z-index: -1;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.add__img {
  width: 40px;
  height: 40px;
  animation: rotate 2s infinite;
}

.add__img__text {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 100%;
  color: #337d5a;
  margin: 0;
}
@media (max-width: 600px) {
  .add__img__text {
    font-size: 12px;
  }
}

.arrowup {
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out;
  position: fixed;
  z-index: 999;
  right: 10px;
  bottom: 100px;
  cursor: pointer;
}
.arrowup:hover {
  transform: scale(0.9);
}
.arrowup:active {
  transform: scale(0.9) translateY(5px);
}
.arrowup.active {
  visibility: visible;
  opacity: 1;
}
@media (max-width: 400px) {
  .arrowup {
    bottom: 70px;
  }
}

.arrowup__img {
  width: 120px;
  height: 120px;
}
@media (max-width: 600px) {
  .arrowup__img {
    width: 90px;
    height: 90px;
  }
}